import { BrowserModule } from '@angular/platform-browser';
import { ErrorHandler, NgModule } from '@angular/core';
import { IonicApp, IonicErrorHandler, IonicModule } from 'ionic-angular';

import { AngularFireModule } from 'angularfire2';
import { AngularFireDatabaseModule } from 'angularfire2/database';

import { MyApp } from './app.component';
import { HomePage } from '../pages/home/home';
import {SobrePage} from '../pages/sobre/sobre';
import {ContatoPage} from '../pages/contato/contato';
import {CardapioPage } from '../pages/cardapio/cardapio';
import {ProdutoPage} from '../pages/produto/produto';

import {HttpModule} from '@angular/http';

import { StatusBar } from '@ionic-native/status-bar';
import { SplashScreen } from '@ionic-native/splash-screen';
export const environment = {
  production: false,
  firebase: {
    apiKey: "AIzaSyBOI3O6OXOZpH_ohg5Yi87KD0GBqwR2ITs",
    authDomain: "lacremecafee.firebaseapp.com",
    databaseURL: "https://lacremecafee.firebaseio.com",
    projectId: "lacremecafee",
    storageBucket: "lacremecafee.appspot.com",
    messagingSenderId: "831799073619"
  }
};

@NgModule({
  declarations: [
    MyApp,
    HomePage,
    ContatoPage,
    SobrePage,
    CardapioPage,
    ProdutoPage
  ],
  imports: [
    HttpModule,
    BrowserModule,
    IonicModule.forRoot(MyApp),
    AngularFireModule.initializeApp(environment.firebase),
    AngularFireDatabaseModule
  ],
  bootstrap: [IonicApp],
  entryComponents: [
    MyApp,
    HomePage,
    ContatoPage,
    SobrePage,
    CardapioPage,
    ProdutoPage
  ],
  providers: [
    HttpModule,
    StatusBar,
    SplashScreen,
    {provide: ErrorHandler, useClass: IonicErrorHandler}
  ]
})
export class AppModule {}



