import { Component } from '@angular/core';
import { NavController } from 'ionic-angular';

import {CardapioPage } from '../cardapio/cardapio';
import {ContatoPage} from '../contato/contato';
import {SobrePage} from '../sobre/sobre';


@Component({
  selector: 'page-home',
  templateUrl: 'home.html'
})
export class HomePage {

  constructor(public navCtrl: NavController,) {

  }
  public abrirContato() {
    this.navCtrl.setRoot(ContatoPage);
  }
  public abrirCardapio() {
    this.navCtrl.setRoot(CardapioPage);
  }
  public abrirSobre() {
    this.navCtrl.setRoot(SobrePage);
  }
  public abrirFacebook(){
    window.open('https://www.facebook.com/lacremecaffe.cafeteria.3','_blank');
  }

}



