import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';
import {FirebaseListObservable,AngularFireDatabase} from "angularfire2/database";
import {ProdutoPage} from '../produto/produto';
@IonicPage()
@Component({
  selector: 'page-cardapio',
  templateUrl: 'cardapio.html',
})
export class CardapioPage {
 
  bebidas: Array<{tipo:string,
                  conteudo :Array<{
                                    imagem:string, 
                                    nome:string,
                                    preco:string,
                                    ingredientes: string,
                                    recomendacoes:Array<any>,
                                    avaliacoes:Array<any>
                                  }>
                  }>;
  comidas: Array<{tipo:string,
                    conteudo :Array<{
                                      imagem:string, 
                                      nome:string,
                                      preco:string,
                                      ingredientes: string,
                                      recomendacoes:Array<any>,
                                      avaliacoes:Array<any>
                                    }>
                    }>;
  aba : string = "bebidas";
  constructor(public navCtrl: NavController, public navParams: NavParams,db : AngularFireDatabase) {
    
    this.bebidas = new Array<{tipo:string,
                              conteudo :Array<{
                                        imagem:string, 
                                        nome:string,
                                        preco:string,
                                        ingredientes : string,
                                        recomendacoes:Array<any>,
                                        avaliacoes:Array<any>
                              }>
                            }>();
      this.comidas = new Array<{tipo:string,
                              conteudo :Array<{
                                        imagem:string, 
                                        nome:string,
                                        preco:string,
                                        ingredientes : string,
                                        recomendacoes:Array<any>,
                                        avaliacoes:Array<any>
                              }>
                            }>();
    db.list("/Comidas").forEach(e => {
          e.forEach(b =>{
            this.comidas.push({
              tipo: b.tipo,
              conteudo: b.Conteudo
              });
          })
          
          
          });  
    db.list("/Bebidas").forEach(e => {
        e.forEach(b =>{
          this.bebidas.push({
            tipo: b.tipo,
            conteudo: b.Conteudo
            });
        })
        });  
    
    

  }
  public verBebida(bebida : any) {
    
    this.navCtrl.push(ProdutoPage,{'imagem': bebida.imagem,
                                    'nome':bebida.nome,
                                    'preco':bebida.preco,
                                    'recomendacoes':bebida.recomendacoes,
                                    'avaliacoes':bebida.avaliacoes,
                                    'ingredientes':bebida.ingredientes});
  }
  ionViewDidLoad() {
    console.log('ionViewDidLoad CardapioPage');
  }

}



