import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';
import {AngularFireDatabase} from "angularfire2/database";
import {Http} from '@angular/http';
import { AlertController } from 'ionic-angular';



@IonicPage()
@Component({
  selector: 'page-contato',
  templateUrl: 'contato.html',
})
export class ContatoPage {
  nome : string;
  mail:string;
  assunto: string;
  mensagem: string;
  constructor(public alertCtrl: AlertController,private http : Http, public navCtrl: NavController, public navParams: NavParams) {
  }

  public enviar(){
    console.log("foi")
    this.http.post('/post.php',{'mail':this.mail,'assunto':this.assunto,'mensagem':this.mensagem}).subscribe( data =>{
      let alert = this.alertCtrl.create({
        title: 'Sucesso',
        subTitle: 'Sua Mensagem Foi Enviada',
        buttons: ['OK']
      });
      alert.present();
    },error =>{
      let alert = this.alertCtrl.create({
        title: 'Erro',
        subTitle: 'Algo Inesperado Aconteceu',
        buttons: ['OK']
      });
      alert.present();
    });
  }
}



